.card-wrapper {
    margin-bottom: 28px;
    border-radius: 7px;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
        background: rgba(65, 212, 129, 0.225289);
        position: relative;

        &::before {
            content: '';
            background-image: url('../../assets/images/cardImages/top.png');
            background-repeat: no-repeat;
            background-position: 0 0;
            position: absolute;
            width: 99px;
            height: 190px;
            margin: auto;
            left: -5px;
            top: -10px;
        }

        &::after {
            content: 'TOP';
            position: absolute;
            margin: auto;
            top: 12px;
            left: 15px;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            color: #000000;
            transform: rotate(-41deg);
        }
    }


}

.wrap-item {
    width: 275px;
    box-sizing: border-box;
    margin-bottom: 28px;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
    overflow: hidden;
    transition: all ease 0.5s;

    &:hover {
        opacity: 0.5;
    }


    .card-img {
        margin-bottom: 10px;
        object-fit: contain;
        width: 275px;
    }

    .card-item {
        padding-left: 10px;
        margin-bottom: 10px;

        .card-title {
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            width: 254px;
            height: 53px;
        }

        .card-data {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            color: #4A4A4A;
            margin-bottom: 5px;
        }

        .card-price {
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            color: #000000;
        }
    }
}

.card-wrap:nth-child(2) {
    background: rgba(65, 212, 129, 0.225289);
}