.footer {
    background: #33383C;

    &-inner {
        &-top {
            background: #282B30;
            padding: 22px 0;

            &-inner {
                display: flex;
                align-items: center;
                justify-content: space-between;

                &-left {
                    display: flex;
                    align-items: center;

                    .logo-text {
                        margin-left: 10px;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 13px;
                        line-height: 17px;
                        color: #FFFFFF;
                        mix-blend-mode: normal;
                        width: 149px;
                    }
                }

                &-middle {
                    .middle-text {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: 18px;
                        color: #98A4B7;
                    }
                }

                &-right {
                    display: flex;
                    align-items: center;

                    .apple {
                        margin-right: 4px;
                    }
                }
            }
        }

        &-middle {
            padding: 52px 0 0px 0;
            display: flex;
            justify-content: space-between;

            .title {
                font-style: normal;
                font-weight: bold;
                font-size: 14px;
                line-height: 17px;
                color: #FFFFFF;
                position: relative;

                &::before {
                    position: absolute;
                    content: '';
                    margin: auto;
                    top: 0;
                    bottom: 0;
                    left: -18px;
                    background-image: url('../../assets/images/footer-icons/redArr.svg');
                    background-repeat: no-repeat;
                    background-position: 0 0;
                    width: 5.9px;
                    height: 9.6px;
                }
            }

            .dom.title:before {
                background-image: url('../../assets/images/footer-icons/greenArr.svg');
            }

            .ned.title:before {
                background-image: url('../../assets/images/footer-icons/yellowArr.svg');
            }

            .tov.title:before {
                background-image: url('../../assets/images/footer-icons/noArr.svg');
            }

            .sport.title:before {
                background-image: url('../../assets/images/footer-icons/greArr.svg');
            }

            .lich.title:before {
                background-image: url('../../assets/images/footer-icons/yelArr.svg');
            }

            .biznes.title:before {
                background-image: url('../../assets/images/footer-icons/blueArr.svg');
            }

            .footer-links {
                list-style-type: none;

                a {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 29px;
                    color: #878F99;
                    &:hover{
                        color: #05F3B3;
                    }
                }
            }
        }

        .bolshi {
            font-size: 15px;
            line-height: 18px;
            color: #66717A;
            text-align: center;
            margin: 0 auto;
            cursor: pointer;
            position: relative;
            width: 100px;
            margin-bottom: 34px;

            &:before {
                position: absolute;
                content: '';
                margin: auto;
                width: 11px;
                height: 5.5px;
                top: 0;
                bottom: 0;
                right: -34px;
                background-image: url('../../assets/images/footer-icons/Fill.svg');
                background-repeat: no-repeat;
                background-position: 0 0;

            }
        }

        &-bottom {
            display: flex;
            justify-content: space-between;
            padding: 27px 0;
            border-top: 1px solid #4C565E;

            .footer-link {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 29px;
                color: #878F99;

                &:hover {
                    color: #05F3B3;
                }
            }
        }
    }
}