 .category-dropdown-menu {
    background: #ffffff;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
    border: 1px solid #efefef;
    padding: 23px 26px;
    margin-top: 30px;
    transition: all .4s linear;
}
 .category1 {
    position: relative;
    display: none;
    &.active{
        display: block;
    }
}
 .category2 {
    position: relative;
    display: none;
    .komp{
        display: flex;
        align-items: center;

        .arrow-img{
            cursor: pointer;
            margin-right: 9px;
        }
        h2{
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            letter-spacing: -6.7332e-05px;
            text-transform: uppercase;
            color: #000000;
            margin-right: 25px;
        }
        .modal-link{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            letter-spacing: -6.7332e-05px;
            text-decoration-line: underline;
            color: #4E72CA;
        }
    }
    &.active{
        display: block;
    }
}
.modal-dr{
    position: absolute;
    z-index: 4;
    width: 1176px;
}
.modal-drop{
    position: absolute;
    z-index: 4;
    width: 1176px;
}

 .part25 {
    width: 25%;
}
 .category-dropdown-item {
    display: table-cell;
    vertical-align: top;
    padding: 10px 0;
    list-style: none;
    font-size: 15px;
    line-height: 20px;
    margin-top: -20px;
}
.category1:before {
    content: "";
    display: block;
    border: 12px solid transparent;
    border-bottom-color: #ffffff;;
    position: absolute;
    top: -24px;
    left: 3%;
}
.category-dropdown-body {
    display: table;
}

 .full {
    width: 100%!important;
    box-sizing: border-box;
    overflow: hidden;
    padding-left: 15px;
}
 .category-dropdown-item li {
    width: 278px;
    margin-top: 20px;
    padding-right: 24px;
    position: relative;
    padding-left: 24px;
}
 .category-dropdown-item li::before {
    position: absolute;
    content: '';
    background-image: url('../../assets/images/icons/aylana.svg');
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: contain;
    width: 10px;
    height: 10px;
    margin: auto;
    top: 0;
    bottom: 0; 
    left: 0;
    
}
    a {
    color: #030303;
}
.box{
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 0;
    transition: all ease 0.5s;
}
.box.active{
    width: 100%;
    height: 100vh;
    opacity: 0.4;
    background: rgb(27, 26, 26);
    position: fixed;
}