.wrapper{
    display: flex;
    border-radius: 7px;
}
.wrap-item {
    width: 275px;
    box-sizing: border-box;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
    border-radius: 7px;
    overflow: hidden;
    transition: all ease 0.5s;
    &:hover{
        opacity: 0.5;
    }
    .card-img {
        margin-bottom: 10px;
        object-fit: contain ;
        width: 275px;
    }

    .card-item {
        padding-left: 10px;
        margin-bottom: 10px;

        .card-title {
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            width: 254px;
            height: 53px;
        }

        .card-data {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            color: #4A4A4A;
            margin-bottom: 5px;
        }

        .card-price {
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            color: #000000;
        }
    }
}