.header-wrap {
    padding-top: 30px;
    padding-bottom: 17px;
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
    margin-bottom: 37px;

    .header {
        &-top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 16px;

            &-left {
                display: flex;
                align-items: center;

                .header-text {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 15px;
                    line-height: 17px;
                    color: #000000;
                    margin-left: 14px;
                }
            }

            &-middle {
                width: 424px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                a {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000;
                }
            }

            &-right {
                display: flex;
                align-items: center;

                .lang {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 17px;
                    color: #4A4A4A;
                    cursor: pointer;
                    position: relative;
                    &::before{
                        content: '';
                        position: absolute;
                        background-image: url('../../assets/images/icons/linee.png');
                        background-repeat: no-repeat;
                        margin: auto;
                        width: 31px;
                        height: 35px;
                        left: 0;
                        top: -3px;
                    }
                }

                .login {
                    display: flex;
                    align-items: center;
                    margin-left: 66px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 20px;

                    .author {
                        display: flex;
                        align-items: center;

                        &-name {
                            font-style: normal;
                            font-weight: 500;
                            font-size: 17px;
                            line-height: 21px;
                            text-align: center;
                            color: #787993;
                            margin-right: 13px;
                        }
                    }

                    h5 {
                        color: #4E72CA;

                    }

                    .pro {
                        margin: 0 20px;
                    }

                    .reg {
                        mix-blend-mode: normal;
                        border-bottom: 1px solid #4E72CA;
                    }
                }
            }
        }

        &-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;

            &.active {
                display: none;
            }

            &-left {
                display: flex;
                background: #FFFFFF;
                border: 1px solid rgba(151, 151, 151, 0.496575);
                box-sizing: border-box;
                border-radius: 22.5px;
                box-sizing: border-box;
                overflow: hidden;
                padding-left: 30px;
                padding-right: 96px;
                position: relative;

                select {
                    border: none;
                    outline: none;
                    background-color: transparent;
                    padding: 12px 0;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000;
                    padding-right: 14px;

                }

                input {
                    height: 44px;
                    font-weight: 500;
                    border: none;
                    border-left: 1px solid rgba(151, 151, 151, 0.370273);
                    border-right: 1px solid rgba(151, 151, 151, 0.370273);
                    padding-left: 25px;
                    padding-right: 19px;
                    width: 358px;
                    margin: 0 22px;
                    outline: none;
                }

                input::placeholder {
                    font-size: 15px;
                    line-height: 18px;
                    color: #000000;
                    left: 19px;
                }

                .btn {
                    background: #19D476;
                    border-radius: 22px;
                    outline: none;
                    border: none;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                    padding: 13px 18px;
                    margin-left: 18px;
                    position: absolute;
                    margin: auto;
                    top: 0;
                    bottom: 0;
                    right: -1px;
                }
            }

            &-right {
                .btn-add {
                    background: #19D476;
                    border-radius: 22px;
                    outline: none;
                    border: none;
                    display: flex;
                    align-items: center;
                    box-sizing: border-box;
                    overflow: hidden;
                    position: relative;

                    .add {
                        padding: 12px 30px 12px 56px;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 15px;
                        line-height: 19px;
                        color: #FFFFFF;
                    }

                    .plus-wrap {
                        width: 46px;
                        height: 46px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #11B463;
                        border-radius: 22px;
                        position: absolute;
                        left: -1px;
                    }
                }
            }
        }

        &-lich {
            .top {
                margin-top: 31px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .left {
                    h3 {
                        font-style: normal;
                        font-weight: bold;
                        font-size: 17px;
                        line-height: 21px;
                        text-align: center;
                        color: #414D6A;
                    }
                }

                .right {
                    display: flex;
                    align-items: center;

                    h4 {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        text-align: center;
                        color: #304FFF;
                        margin-right: 26px;
                    }

                    button {
                        font-style: normal;
                        font-weight: 700;
                        font-size: 14px;
                        line-height: 19px;
                        color: #000000;
                        width: 150px;
                        height: 37px;
                        position: relative;
                        background: #FFFFFF;
                        border: 1px solid #41D481;
                        box-sizing: border-box;
                        border-radius: 100px 35px 35px 100px;
                        padding-left: 42px;

                        span {
                            position: absolute;
                            width: 34px;
                            height: 34px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background: #41D481;
                            border-radius: 50%;
                            top: 0;
                            left: 0;
                            margin: auto;
                        }
                    }
                }
            }
        }
    }
}