.footer-card{
    padding-top: 26px;
}
.links-card {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #D5D5D5;
    margin-bottom: 7px;
    .link {
        margin-right: 99px;
        padding-bottom: 10px;
        border-bottom: 1px solid transparent;
        font-style: normal;
        font-weight: 900;
        font-size: 24px;
        line-height: 30px;
        color: #9B9B9B;

        &.active {
            color: #4E72CA;
            border-bottom: 1px solid #4E72CA;
        }

    }
}
.card-f{
    display: none;
    &.active{
        display: block;
    }
}

.card-wrapp{
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    overflow-x: scroll;
    box-sizing: border-box;
    
    .wrap-item{
        border: 1px solid rgba(120, 121, 147, 0.243461);
        box-sizing: border-box;
        box-shadow: none;
        width: 367px;
        height: 391px;
        padding: 30px ;
        margin-right: 31px;
        .card-img{
            width: 309px;
        }
    }

}