.card-wrap{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid rgba(182, 202, 251, 0.606827);
    margin-bottom: 18px;

}
.bolshi{
    font-size: 15px;
    line-height: 18px;
    color: #4E72CA;
    text-align: center;
    margin: 0 auto;
    cursor: pointer;
    position: relative;
    width: 180px;
    margin-bottom: 20px;
    &::before{
        position: absolute;
        content: "";
        margin: auto;
        width: 12px;
        height: 6.5px;
        top: 0px;
        bottom: 0;
        right: 5px;
        background-image: url('../../assets/images/HomeImg/Fill.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
    
    }
}